/* ‚úÖ Ï†ÑÏ≤¥ Î†àÏù¥ÏïÑÏõÉ */
.post-feed {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}

/* ‚úÖ ÏôºÏ™Ω ÏÇ¨Ïù¥ÎìúÎ∞î */
.sidebar {
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: fixed;
  left: 20px;
  top: 100px;
}

.nav-link, .search-button {
  background: none;
  border: none;
  text-decoration: none;
  font-size: 16px;
  color: black;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s, color 0.2s;
}

.nav-link:hover, .search-button:hover {
  background: #f0f0f0;
  color: #007bff;
}

/* ‚úÖ Í≤åÏãúÎ¨º Ïª®ÌÖåÏù¥ÎÑà */
.posts-container {
  display: flex;
  flex-direction: column;
  width: 600px;
  gap: 20px;
}

/* ‚úÖ Í≤åÏãúÎ¨º Î∞ïÏä§ */
.post {
  background: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  width: 100%;
}

/* ‚úÖ Í≤åÏãúÎ¨º Ìó§Îçî */
.post-header {
