/* ‚úÖ Ïù¥ÎØ∏ÏßÄ Ïä¨ÎùºÏù¥Îçî */
.image-slider {
  position: relative;
  width: 100%;
  max-height: 350px; /* ÏÑ∏Î°ú ÌÅ¨Í∏∞ ÌÇ§ÏõÄ */
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.post-image {
  width: 100%;
  height: 350px; /* ÌÅ¨Í∏∞ ÌÇ§ÏõÄ */
  object-fit: cover;
  border-radius: 5px;
}

/* üî• Ïä¨ÎùºÏù¥Îçî Ïª®Ìä∏Î°§ (ÌôîÏÇ¥Ìëú) */
.slider-controls {
